
"use client";

import { useState, useMemo, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Calendar as CalendarIcon, PlusCircle, Trash2, Loader2, CircleSlash } from "lucide-react";
import { useDatabase } from '@/context/database-context';
import { Semester, ExclusionDate } from "@/lib/types";
import { format } from 'date-fns';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter, DialogTrigger } from "@/components/ui/dialog";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Calendar } from "@/components/ui/calendar";
import { cn } from "@/lib/utils";
import { useToast } from '@/hooks/use-toast';

// Add Exclusion Dialog Component
const exclusionSchema = z.object({
  reason: z.string().min(3, "Reason must be at least 3 characters."),
  dateRange: z.object({
    from: z.date({ required_error: "A start date is required."}),
    to: z.date({ required_error: "An end date is required."}),
  }),
});

type ExclusionFormValues = z.infer<typeof exclusionSchema>;

function AddExclusionDialog({ semester, onExclusionAdded }: { semester: Semester, onExclusionAdded: () => void }) {
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();
  const { updateSemester } = useDatabase();

  const form = useForm<ExclusionFormValues>({
    resolver: zodResolver(exclusionSchema),
  });

  const onSubmit = async (data: ExclusionFormValues) => {
    setIsLoading(true);
    const newExclusion: ExclusionDate = {
      id: `EXC-${Date.now()}`,
      reason: data.reason,
      startDate: format(data.dateRange.from, 'yyyy-MM-dd'),
      endDate: format(data.dateRange.to, 'yyyy-MM-dd'),
    };

    const updatedExclusions = [...(semester.exclusionDates || []), newExclusion];

    const success = await updateSemester(semester.id, { exclusionDates: updatedExclusions });
    
    setIsLoading(false);

    if (success) {
      toast({ title: "Exclusion Added", description: "The new exclusion rule has been saved." });
      onExclusionAdded();
      setIsOpen(false);
      form.reset();
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogTrigger asChild>
        <Button>
          <PlusCircle className="mr-2" /> Add Exclusion Rule
        </Button>
      </DialogTrigger>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>Add New Exclusion Rule</DialogTitle>
          <DialogDescription>
            Select a date range and provide a reason for the exclusion. No classes will be schedulable during this period.
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
            <FormField
              control={form.control}
              name="reason"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Reason</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g., National Day Holiday" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="dateRange"
              render={({ field }) => (
                <FormItem className="flex flex-col">
                  <FormLabel>Date Range</FormLabel>
                   <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        id="date"
                        variant={"outline"}
                        className={cn(
                          "w-full justify-start text-left font-normal",
                          !field.value?.from && "text-muted-foreground"
                        )}
                      >
                        <CalendarIcon className="mr-2 h-4 w-4" />
                        {field.value?.from ? (
                          field.value.to ? (
                            <>
                              {format(field.value.from, "LLL dd, y")} -{" "}
                              {format(field.value.to, "LLL dd, y")}
                            </>
                          ) : (
                            format(field.value.from, "LLL dd, y")
                          )
                        ) : (
                          <span>Pick a date range</span>
                        )}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0" align="start">
                      <Calendar
                        initialFocus
                        mode="range"
                        defaultMonth={field.value?.from}
                        selected={field.value}
                        onSelect={field.onChange}
                        numberOfMonths={2}
                      />
                    </PopoverContent>
                  </Popover>
                  <FormMessage />
                </FormItem>
              )}
            />
            <DialogFooter>
              <Button type="submit" disabled={isLoading}>
                {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                Save Rule
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}


// Main Page Component
export default function ExclusionsPage() {
  const { semesters, loading: dbLoading, updateSemester } = useDatabase();
  const { toast } = useToast();
  const [selectedSemesterId, setSelectedSemesterId] = useState<string | null>(null);
  const [forceUpdate, setForceUpdate] = useState(0);

  const selectedSemester = useMemo(() => {
    return semesters.find(s => s.id === selectedSemesterId);
  }, [semesters, selectedSemesterId]);

  useEffect(() => {
    if (!selectedSemesterId && semesters.length > 0) {
      setSelectedSemesterId(semesters[0].id);
    }
  }, [semesters, selectedSemesterId]);

  const handleDeleteExclusion = async (exclusionId: string) => {
    if (!selectedSemester) return;

    const updatedExclusions = selectedSemester.exclusionDates?.filter(ex => ex.id !== exclusionId) || [];
    const success = await updateSemester(selectedSemester.id, { exclusionDates: updatedExclusions });
    
    if(success) {
      toast({ title: "Exclusion Removed", description: "The rule has been successfully deleted." });
      setForceUpdate(f => f + 1); // This will trigger re-renders if needed
    }
  };

  const onExclusionAdded = () => {
    setForceUpdate(f => f + 1);
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold font-headline flex items-center gap-2">
          <CircleSlash className="w-8 h-8" />
          Exclusion Rules
        </h1>
        {selectedSemester && <AddExclusionDialog semester={selectedSemester} onExclusionAdded={onExclusionAdded}/>}
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Manage Exclusions</CardTitle>
          <CardDescription>Select a semester to view or modify its exclusion rules.</CardDescription>
        </CardHeader>
        <CardContent>
          {dbLoading ? (
            <div className="flex justify-center items-center h-24">
              <Loader2 className="h-8 w-8 animate-spin text-primary" />
            </div>
          ) : (
            <Select value={selectedSemesterId ?? ""} onValueChange={setSelectedSemesterId}>
              <SelectTrigger className="w-full md:w-1/3">
                <SelectValue placeholder="Select a semester" />
              </SelectTrigger>
              <SelectContent>
                {semesters.map(s => <SelectItem key={s.id} value={s.id}>{s.name}</SelectItem>)}
              </SelectContent>
            </Select>
          )}
        </CardContent>
      </Card>
      
      {selectedSemester && (
        <Card>
          <CardHeader>
            <CardTitle>Exclusion List for {selectedSemester.name}</CardTitle>
          </CardHeader>
          <CardContent>
            {selectedSemester.exclusionDates && selectedSemester.exclusionDates.length > 0 ? (
                <ul className="space-y-3">
                    {selectedSemester.exclusionDates.map(ex => (
                        <li key={ex.id} className="flex justify-between items-center p-3 border rounded-md bg-muted/50">
                           <div>
                             <p className="font-semibold">{ex.reason}</p>
                             <p className="text-sm text-muted-foreground">{format(new Date(ex.startDate), 'PPP')} - {format(new Date(ex.endDate), 'PPP')}</p>
                           </div>
                           <Button variant="destructive" size="icon" onClick={() => handleDeleteExclusion(ex.id)}>
                               <Trash2 className="h-4 w-4" />
                           </Button>
                        </li>
                    ))}
                </ul>
            ) : (
                <p className="text-center text-muted-foreground py-8">No exclusion rules have been set for this semester.</p>
            )}
          </CardContent>
        </Card>
      )}

    </div>
  );
}
